import styled from "styled-components";

export const Container = styled.div`
  max-height: 1300px;

  @media only screen and (min-width: 1300px) and (max-width: 1520px) {
    max-height: 1800px;
  }
  @media only screen and (min-width: 665px) and (max-width: 980px) {
    max-height: 1800px;
  }
  @media only screen and (max-width: 665px) {
    max-height: fit-content;
  }
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: right;
  align-items: center;

  .dashed {
    fieldset {
      p {
        font-size: small;
      }
      div:not(:last-child) {
        border-bottom: 1px dotted gray;
      }
    }
  }

  .pizza {
    fieldset {
      div {
        p:nth-child(2) {
          display: flex;
          flex-direction: column;
        }
        hr {
          margin: 0.5rem 0;
        }
      }
    }
  }
`;

export const Div = styled.div`
  height: fit-content;
  width: 300px;
  @media only screen and (max-width: 665px) and (min-width: 0px) {
    max-width: 98%;
  }
  @media only screen and (min-width: 1520px) {
    width: 32%;
  }
  margin: 1rem 0.5rem;
  margin-bottom: auto;

  i {
    font-size: small;
    color: darkkhaki;
  }
  fieldset {
    padding: 0.5rem 1rem;

    img {
      width: 265px;
    }
  }

  legend {
    padding: 0 0.5rem;
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    color: blanchedalmond;
  }
`;
