import styled from "styled-components";

export const Container = styled.div`
  position: relative;
  width: 100%;
  color: ghostwhite;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
  padding: 0 1rem;

  fieldset {
    padding: 1rem;
    max-width: 1200px;
    margin-top: 2rem;
    flex-shrink: 1;
  }

  legend {
    margin-left: 1rem;
    padding: 1rem;
    font-size: 1.5rem;
    font-style: italic;
  }

  table,
  th,
  td {
    border: 1px solid palegoldenrod;
    padding: 0.2rem 0.5rem;
    vertical-align: middle;
  }

  tr {
    /* display: flex; */
  }
  th {
    background: rgba(255, 255, 255, 0.1);
  }

  td {
    display: flex;
    gap: 1rem;
    :first-child {
      flex: 1;
    }
  }

  a {
    color: darkgoldenrod;
  }
  i {
    max-width: 70ch;
    font-size: small;
  }

  h1,
  h2 {
    font-style: italic;
  }

  h2 {
    margin: 2rem 0 1rem 0;
  }

  li {
    list-style-type: circle;
  }
`;
