import React, { useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, {
  Navigation,
  Pagination,
  EffectCube,
  Autoplay,
} from "swiper";
import "swiper/swiper-bundle.css";
import "swiper/components/effect-cube/effect-cube.scss";
import "swiper/components/lazy/lazy.scss";
import { Container } from "./Style";
import { Img } from "../../../../GlobalStyles";

SwiperCore.use([Navigation, Pagination, EffectCube, Autoplay]);

function Swipe() {
  const params = {
    loop: true,
    speed: 2000,
    autoplay: {
      delay: 4000,
    },
  };

  return (
    <Container>
      <Swiper
        {...params}
        id="main"
        effect="cube"
        tag="section"
        wrapperTag="ul"
        navigation
        pagination
        spaceBetween={0}
        slidesPerView={1}
        onInit={(swiper) => console.log("Swiper initialized!", swiper)}
        onSlideChange={(swiper) => {
          console.log("Slide index changed to: ", swiper.activeIndex);
        }}
        onReachEnd={() => console.log("Swiper end reached")}
      >
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/67/45/P029236745/P029236745_480.jpg" />
          <p>Private room 3 people x 1</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/69/22/P029236922/P029236922_480.jpg" />
          <p>Table 4 people x 5</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/67/87/P029236787/P029236787_480.jpg" />
          <p>Table 4 people x 3</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/74/38/P029237438/P029237438_480.jpg" />
          <p>Table 4 people x 3</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/68/16/P029236816/P029236816_480.jpg" />
          <p>Counter 6 people x 1</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/68/33/P029236833/P029236833_480.jpg" />
          <p>Loft seat 12 people x 1</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/74/50/P029237450/P029237450_480.jpg" />
          <p>Loft seat 12 people x 1</p>
        </SwiperSlide>
        <SwiperSlide>
          <Img src="https://imgfp.hotp.jp/IMGH/69/96/P029236996/P029236996_480.jpg" />
          <p>Table 20 people x 1</p>
        </SwiperSlide>
      </Swiper>
    </Container>
  );
}

export default Swipe;
