import styled from "styled-components";

export const Div = styled.div`
  position: relative;
  height: fit-content;
  width: 100%;
  background-image: linear-gradient(
      135deg,
      rgba(107, 107, 107, 0.06) 0%,
      rgba(107, 107, 107, 0.06) 50%,
      rgba(202, 202, 202, 0.06) 50%,
      rgba(202, 202, 202, 0.06) 100%
    ),
    linear-gradient(90deg, rgb(20, 20, 20), rgb(20, 20, 20));
  background-size: 30px 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 5vmax 0;
  color: ghostwhite;
  /* scroll-snap-align: start; */

  iframe {
    width: calc(30vmax + 50px);
    height: calc(30vmax + 50px);
    margin-bottom: 2rem;
  }
`;
export const Info = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: left;
  button {
    width: fit-content;
    margin: auto;
  }

  li {
    font-size: calc(1vmax + 0.5rem);
    margin: 0.5rem 0;
    pointer-events: none;
    display: flex;
    justify-content: left;

    b {
      .MuiSvgIcon-root {
        fill: #772e25 !important;
        background: radial-gradient(
          circle,
          rgba(255, 255, 255, 1) 1%,
          rgba(255, 255, 255, 0) 80%
        );
      }

      padding: 0.4vmax 1vmax;

      width: auto;
    }
  }
`;
