import styled from "styled-components/macro";

export const Container = styled.div`
  width: 100%;
  position: relative;

  .footer {
    scroll-snap-align: start;
  }
`;

export const Banner = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  position: relative;
  z-index: 1;

  :after {
    content: "";
    width: 100%;
    height: 110vh;
    position: absolute;
    top: -50px;
    background: radial-gradient(
      circle,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 0.711922268907563) 50%
    );
    z-index: 1;
  }

  img {
    z-index: 1;
    width: calc(25vmax + 100px);
    height: auto;
    position: relative;
    transform: translateY(-5vmax);
  }
`;

export const BlurTop = styled.div`
  background: linear-gradient(to top, rgba(0, 0, 0, 1) 10%, rgba(0, 0, 0, 0));
  bottom: -1px;
  height: 30vh;
  width: 100%;
  position: absolute;
`;
export const BlurBot = styled.div`
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 1) 10%,
    rgba(0, 0, 0, 0)
  );
  height: 30vh;
  width: 100%;
  position: absolute;
  top: -1px;
`;

export const Menu = styled.div`
  background-image: linear-gradient(
      135deg,
      transparent 0%,
      transparent 26%,
      rgba(151, 151, 151, 0.07) 26%,
      rgba(151, 151, 151, 0.07) 39%,
      transparent 39%,
      transparent 100%
    ),
    linear-gradient(
      45deg,
      transparent 0%,
      transparent 48%,
      rgba(151, 151, 151, 0.07) 48%,
      rgba(151, 151, 151, 0.07) 58%,
      transparent 58%,
      transparent 100%
    ),
    linear-gradient(
      0deg,
      transparent 0%,
      transparent 70%,
      rgba(151, 151, 151, 0.07) 70%,
      rgba(151, 151, 151, 0.07) 81%,
      transparent 81%,
      transparent 100%
    ),
    linear-gradient(
      135deg,
      transparent 0%,
      transparent 32%,
      rgba(151, 151, 151, 0.07) 32%,
      rgba(151, 151, 151, 0.07) 33%,
      transparent 33%,
      transparent 100%
    ),
    linear-gradient(90deg, rgb(0, 0, 0), rgb(0, 0, 0));
  width: 100%;
  min-height: 100vh;
  height: fit-content;
  display: flex;
  justify-content: space-around;
  align-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 5vmax 0;
  scroll-snap-align: start;

  *,
  *:before,
  *:after {
    transition: all 0.4s ease-out;
  }
`;

export const MenuLink = styled.div`
  max-width: 480px;
  width: calc(20vmax + 100px);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
  h3 {
    position: relative;
    max-width: 30ch;
    text-align: center;
  }

  :after,
  :before {
    content: "";
    position: absolute;
    visibility: hidden;
    opacity: 0;
  }

  :after {
    width: calc(20vmax + 100px);
    height: calc(20vmax + 100px);
    background: rgba(0, 0, 0, 0.3);
  }

  :before {
    width: calc(16vmax + 100px);
    height: calc(16vmax + 100px);
    background: rgba(0, 0, 0, 0.5);
    z-index: 1;
  }

  img {
    /* object-fit: contain; */
    position: relative;
    max-width: 480px;
    width: calc(20vmax + 100px);
  }

  section {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    :before,
    :after {
      content: "";
      width: 10px;
      height: calc(16vmax + 100px);
      width: calc(16vmax + 100px);
      position: absolute;
    }
    :before {
      transform: scaleX(0);
      border-top: 3px solid cornsilk;
      border-bottom: 3px solid cornsilk;
    }
    :after {
      transform: scaleY(0);
      border-left: 3px solid cornsilk;
      border-right: 3px solid cornsilk;
    }

    h3,
    button {
      visibility: hidden;
      opacity: 0;
    }
  }

  :hover {
    img {
      transform: scale(1.1);
    }
    section {
      z-index: 1;
      :before,
      :after {
        transform: scaleX(1);
      }
      h3,
      button {
        visibility: visible;
        opacity: 1;
      }
      button {
        transform: translateY(2vmax);
      }
      h3 {
        transform: translateY(-2vmax);
      }
    }
    :after,
    :before {
      visibility: visible;
      opacity: 1;
    }
  }
`;

export const Reserve = styled.div`
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.5)
    url("https://images.cdn.uniqueway.com/uploads/2016/04/b808782b-b040-4fb2-889b-df7a4d7f23ec.jpg")
    no-repeat fixed center;
  background-size: cover;
  display: flex;
  justify-content: center;
  color: white;
  position: relative;
  background-blend-mode: multiply;
  scroll-snap-align: start;

  div {
    text-align: center;
    gap: 3rem;
    * {
      z-index: 3;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 2vmax;
    padding: 0 1rem;
  }
`;

export const Facilities = styled.div`
  background-image: linear-gradient(
      180deg,
      transparent 0%,
      transparent 11%,
      rgba(156, 156, 156, 0.06) 11%,
      rgba(156, 156, 156, 0.06) 81%,
      transparent 81%,
      transparent 100%
    ),
    linear-gradient(
      180deg,
      transparent 0%,
      transparent 18%,
      rgba(156, 156, 156, 0.06) 18%,
      rgba(156, 156, 156, 0.06) 64%,
      transparent 64%,
      transparent 100%
    ),
    linear-gradient(
      270deg,
      transparent 0%,
      transparent 13%,
      rgba(156, 156, 156, 0.06) 13%,
      rgba(156, 156, 156, 0.06) 22%,
      transparent 22%,
      transparent 100%
    ),
    linear-gradient(225deg, rgb(0, 0, 0), rgb(0, 0, 0));
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 5rem 0;
  scroll-snap-align: center;

  h2 {
    color: ghostwhite;
  }

  > div {
    display: flex;
    justify-content: space-around;
    align-content: space-between;
    flex-wrap: wrap;
    flex: 1;
  }

  @media only screen and (min-width: 700px) and (max-width: 1200px) {
    flex-direction: column;
  }
`;

export const Cube = styled.div`
  position: relative;
  max-width: 480px;
  width: calc(10vmax + 100px);
  height: calc(10vmax + 100px);
  transition: all 0.5s ease-out;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 3vmax 1vmax;

  > img {
    width: 100%;
    height: 100%;
    transition: all 0.5s ease-out;
    transform-origin: bottom;
    z-index: 1;
    color: #ffa500;
    box-shadow: 0 0.2em 0.5rem 0.1rem;
  }

  div {
    width: 100%;
    height: 100%;
    position: absolute;
    transform: translateY(100%) rotateX(90deg);
    transform-origin: top;
    transition: all 0.5s ease-out;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #ffa500;
    box-shadow: 0 1em 0 0.2rem;
    padding: 1rem;
    z-index: 4;

    img {
      position: absolute;
      max-width: calc(9vmax + 100px);
      max-height: calc(9vmax + 100px);
      z-index: -1;
      content: url("/images/logo2.png");
    }

    h2 {
      pointer-events: none;
      font-size: calc(1vmax + 0.2rem);
      z-index: 1;
    }

    :before,
    :after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }

    :before {
      background: rgba(0, 0, 0);
      z-index: -1;
    }
    :after {
      background: rgba(0, 0, 0, 0.5);
      z-index: 0;
    }
  }

  :hover {
    > img {
      top: -100;
      transform: translateY(-100%) rotateX(90deg);
    }
    div {
      transform: translateY(0%) rotateX(0deg);
      box-shadow: 0 0 1rem 0.2rem;
      transform-origin: top;
    }
  }
`;

export const Store = styled.div`
  position: relative;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2vmax;
  padding: 0 1rem;
  scroll-snap-align: start;
  overflow: hidden;

  section {
    div {
      width: calc(50vmax + 100px);
      height: 120vh;
      background: rgba(0, 0, 0, 0.8);
      transform: skew(45deg);
      overflow: hidden;
      display: flex;
      justify-content: center;
      position: relative;
      right: -200px;

      :after {
        content: "";
        display: block;
        width: calc(50vmax + 100px);
        height: 120vh;
        position: absolute;
        background: rgba(0, 0, 0, 0.2);
        top: 0;
      }
      img {
        max-width: 960px;
        width: calc(50vmax + 100px);
        transform: skew(315deg);
        position: relative;
        object-fit: contain;
        @media only screen and (max-width: 500px) {
          top: -100px;
          left: -100px;
        }
      }
    }
  }
  h1 {
    text-shadow: -1px 3px 9px rgba(0, 0, 0, 0.79);
    font-size: calc(1.7vmax + 0.5rem);
    max-width: 25ch;
    margin: 2rem auto;
    z-index: 2;
    transform: translate(50px, -50px);
    text-align: center;
    @media only screen and (max-width: 1000px) {
      position: absolute;
      top: 60%;
      left: 10%;
      max-width: 20ch;
    }
    @media only screen and (max-width: 500px) {
      transform: translate(-20px, -20px);
    }
  }

  h2 {
    font-size: calc(0.8vmax + 0.5rem);
    text-shadow: -1px 3px 9px rgba(0, 0, 0, 0.79);
    width: 60ch;
    transform: translateY(-50px);
    /* margin-left: 100px; */
    position: absolute;
    @media only screen and (max-width: 1000px) {
      display: none;
    }
  }
`;
