import { createGlobalStyle } from "styled-components";
import styled from "styled-components/macro";
import { Link } from "react-router-dom";
import React from "react";

const GlobalStyle = createGlobalStyle`
html,body, #root {
    height: fit-content;
    width: 100%;
    scroll-behavior: smooth;
    scroll-snap-type: y mandatory;
}



*, *::before, *::after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    outline: none;
    text-decoration: none;
    letter-spacing: 0.05rem;
    /* color: ghostwhite; */
    
    
}
a {
  color: ghostwhite;
}


.nav__black {
    background-color: black;
  transition: all 0.5s ease-in-out;
  img {
      transform: translateY(0%);
      transition: all .5s ease-in-out;
      opacity: 1;
  }
      /* @media (max-width: 768px) {
        a:first-child {
        transform: translateY(-200%);
        opacity: 0;
        }
      } */
}
details[open] summary ~ * {
    animation: sweep 0.5s ease-in-out;
  }

  @keyframes sweep {
    0% {
      opacity: 0;
      transform: translateY(-10%);
      opacity: 0;
    }
    100% {
      opacity: 1;
      transform: translateY(0%);
      opacity: 1;
    }
  }


summary {
  cursor: pointer;
  font-weight: bold;
}

.Start {
  transform: translateY(0);
  opacity: 1;
transition: all 0.3s ease-out;
}
`;

export default GlobalStyle;

const Button = styled.button`
  font-size: 1.5rem;
  padding: calc(1vmax) calc(2vmax);
  border: none;
  border: ${({ yellow }) =>
    yellow ? `1px solid rgb(245, 218, 122)` : `1px solid rgb(146, 148, 248)`};
  position: relative;
  overflow: hidden;
  z-index: 100;
  background: transparent;
  cursor: pointer;

  :hover {
    box-shadow: ${({ yellow }) =>
      yellow
        ? ` 1px 1px 25px 10px rgba(245, 218, 122, 0.4)`
        : ` 1px 1px 25px 10px rgba(146, 148, 248, 0.4)`};
  }

  :before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: ${({ yellow }) =>
      yellow
        ? `linear-gradient(
        120deg,
        transparent,
        rgba(245, 218, 122, 0.4),
        transparent
      )`
        : `linear-gradient(
        120deg,
        transparent,
        rgba(168, 169, 240, 0.4),
        transparent
      )`};
    transition: all 650ms;
  }

  :hover:before {
    left: 100%;
  }
`;

export const LinkButton = (props) => (
  <Button {...props}>
    <Link to={props.to}>{props.children}</Link>
  </Button>
);

export const Img = styled.img.attrs((props) => ({
  loading: "lazy",
  alt: "",
}))``;
