import React from "react";
import { LinkButton, Img } from "../../../GlobalStyles";
import {
  Container,
  Banner,
  Menu,
  BlurTop,
  BlurBot,
  MenuLink,
  Reserve,
  Facilities,
  Cube,
  Store,
} from "./Style";

function Body() {
  return (
    <Container>
      <Banner>
        <Img src="/images/logo.svg" />
        <BlurTop />
      </Banner>
      <Menu>
        <MenuLink to="/menu">
          <Img src="https://imgfp.hotp.jp/IMGH/39/75/P029633975/P029633975_480.jpg" />
          <section>
            <h3>
              Let's toast with wine ♪ The banquet course that is Great on the
              day and is perfect for a sudden banquet ★
            </h3>
            <LinkButton yellow to="/menu">
              See Menu
            </LinkButton>
          </section>
        </MenuLink>
        <MenuLink to="/menu">
          <Img src="https://imgfp.hotp.jp/IMGH/93/07/P032259307/P032259307_480.jpg" />
          <section>
            <h3>
              You can choose from all 21 types! Hitokuchi Pizza "Pizza Tapas"
            </h3>
            <LinkButton yellow to="/menu">
              See Menu
            </LinkButton>
          </section>
        </MenuLink>
        <MenuLink to="/menu">
          <Img src="https://imgfp.hotp.jp/IMGH/05/46/P030060546/P030060546_480.jpg" />
          <section>
            <h3>
              Happy hour from 17:00 to 19:00 ★ Drinks can be enjoyed for ¥ 300 ♪{" "}
            </h3>
            <LinkButton yellow to="/menu">
              See Menu
            </LinkButton>
          </section>
        </MenuLink>
      </Menu>
      <Reserve>
        <BlurBot />
        <div>
          <h1>≪3 minutes walk from Morioka Station≫</h1>

          <LinkButton to="/reservation">Reserve Now</LinkButton>
        </div>
        <BlurTop />
      </Reserve>
      <Facilities>
        <div>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/75/49/P029237549/P029237549_480.jpg" />
            <div>
              <h2>
                "Beef tendon stew", which is carefully stewed in a large pot
                next to the counter, is a must-try!
              </h2>
              <Img />
            </div>
          </Cube>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/62/54/P029236254/P029236254_480.jpg" />
            <div>
              <h2>
                We offer dishes using ingredients from the prefecture, including
                Fukuda bread. Please enjoy Iwate's "delicious"!
              </h2>
              <Img />
            </div>
          </Cube>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/13/24/P029221324/P029221324_480.jpg" />
            <div>
              <h2>
                There is also a private room that feels like a hidden room.
                Ideal for those who want to talk quietly and calmly ♪
              </h2>
              <Img />
            </div>
          </Cube>
        </div>
        <div>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/74/99/P029237499/P029237499_480.jpg" />
            <div>
              <h2>
                Morioka citizen's sole food "Fukuda bread" in the izakaya style
                ★
              </h2>
              <Img />
            </div>
          </Cube>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/74/74/P029237474/P029237474_480.jpg" />
            <div>
              <h2>
                Homemade seasonal cider made from seasonal fruits is also
                popular!
              </h2>
              <Img />
            </div>
          </Cube>
          <Cube>
            <Img src="https://imgfp.hotp.jp/IMGH/63/33/P029236333/P029236333_480.jpg" />
            <div>
              <h2>
                We have a large selection of recommended wines, not to mention
                Iwate wines ♪ Please try to find your favorite taste.
              </h2>
              <Img />
            </div>
          </Cube>
        </div>
      </Facilities>
      <Store>
        <BlurBot />
        <div>
          <h1>
            An everyday bar where you can easily go to the bar as many times as
            you like
          </h1>
          <h2>
            　Let's talk with friends today while enjoying the wonderful
            ingredients of Iwate with a glass of wine. 　Our signature menu,
            "Must-eat! Ebiske stewed beef shiji" and "Koppe-pan in that
            clothes", which are always perfect for more than 50 kinds of wine
            and homemade seasonal cider, are especially excellent. 　Banquets
            that can accommodate up to 25 people are also very popular.
          </h2>
        </div>

        <section>
          <div>
            <Img src="http://iwate-inshoku.jp/wp-content/uploads/2015/10/盛岡市部_ワインノエビスケ_ph03.jpg" />
          </div>
        </section>
        <BlurTop />
      </Store>
      <div className="footer"></div>
    </Container>
  );
}

export default Body;
