import React from "react";
import PropTypes from "prop-types";
import SwipeableViews from "react-swipeable-views";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import TrainIcon from "@material-ui/icons/Train";
import { Container } from "./Style";
import LocalBarIcon from "@material-ui/icons/LocalBar";
import LocalDiningIcon from "@material-ui/icons/LocalDining";
import LocalDrinkIcon from "@material-ui/icons/LocalDrink";
import MenuBookIcon from "@material-ui/icons/MenuBook";
import { Main, Side } from "./Style";
import Course from "./Course/Course";
import Cuisine from "./Cuisine/Cuisine";
import Drinks from "./Drinks/Drinks";
import Wine from "./Wine/Wine";
import { Img } from "../../../GlobalStyles";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography component={"span"} variant={"body2"}>
            {children}
          </Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    // backgroundColor: "#321",
    // [theme.breakpoints.up("md")]: {
    //   backgroundColor: "brown",
    // },
    color: "ghostwhite",
  },
}));

export default function FullWidthTabs() {
  const classes = useStyles();
  const theme = useTheme();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleChangeIndex = (index) => {
    setValue(index);
  };

  return (
    <Container className={classes.root}>
      <Side>
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={value}
          onChangeIndex={handleChangeIndex}
        >
          <TabPanel
            className="coursePanel"
            value={value}
            index={0}
            dir={theme.direction}
          >
            {" "}
            <Img src="/images/japcourse.jpg" />
          </TabPanel>
          <TabPanel value={value} index={1} dir={theme.direction}>
            <Img src="https://tblg.k-img.com/restaurant/images/Rvw/119062/640x640_rect_119062556.jpg" />
            <Img src="https://tblg.k-img.com/restaurant/images/Rvw/119062/640x640_rect_119062571.jpg" />
            <Img src="https://tblg.k-img.com/restaurant/images/Rvw/40684/640x640_rect_40684298.jpg" />
          </TabPanel>
          <TabPanel
            className="drinkstab"
            value={value}
            index={2}
            dir={theme.direction}
          >
            <Img className="drinks" src="/images/japdrinks.jpg" />
          </TabPanel>
          <TabPanel
            className="wine"
            value={value}
            index={3}
            dir={theme.direction}
          >
            <Img src="https://image.freepik.com/free-photo/romantic-date-with-wine_236854-13677.jpg" />
            <Img src="https://i.pinimg.com/originals/d2/22/21/d2222132580c831a7cf73d3d53003a75.jpg" />
            <Img src="https://www.thedrinksbusiness.com/wordpress/wp-content/uploads/2014/11/fizz-506x640.jpg" />
            <Img src="https://image.freepik.com/free-photo/two-glasses-champagne-rose_144627-6572.jpg" />
          </TabPanel>
        </SwipeableViews>
      </Side>
      <Main>
        <AppBar className="appBar" position="static">
          <Tabs
            value={value}
            onChange={handleChange}
            indicatorColor="primary"
            textColor="primary"
            variant="fullWidth"
            aria-label="full width tabs example"
          >
            <Tab icon={<MenuBookIcon />} label="Course" {...a11yProps(0)} />
            <Tab icon={<LocalDiningIcon />} label="Cuisine" {...a11yProps(1)} />
            <Tab icon={<LocalDrinkIcon />} label="Drink" {...a11yProps(2)} />
            <Tab icon={<LocalBarIcon />} label="Wine" {...a11yProps(3)} />
          </Tabs>
        </AppBar>
        <SwipeableViews
          axis={theme.direction === "rtl" ? "x-reverse" : "x"}
          index={value}
          onChangeIndex={handleChangeIndex}
        >
          <TabPanel value={value} index={0} dir={theme.direction}>
            <Course />
          </TabPanel>
          <TabPanel value={value} index={1} dir={theme.direction}>
            <Cuisine />
          </TabPanel>
          <TabPanel value={value} index={2} dir={theme.direction}>
            <Drinks />
          </TabPanel>
          <TabPanel value={value} index={3} dir={theme.direction}>
            <Wine />
          </TabPanel>
        </SwipeableViews>
      </Main>
    </Container>
  );
}
