import React from "react";
import styled from "styled-components";
import Footer from "../Footer/Footer";
import Navbar from "../Navbar/Navbar";
import Body from "./Body/Body";

const Container = styled.div`
  position: relative;
  height: fit-content;
  width: 100%;
  background: radial-gradient(
        hsl(0, 100%, 27%) 4%,
        hsl(0, 100%, 18%) 9%,
        hsla(0, 100%, 20%, 0) 9%
      )
      0 0,
    radial-gradient(
        hsl(0, 100%, 27%) 4%,
        hsl(0, 100%, 18%) 8%,
        hsla(0, 100%, 20%, 0) 10%
      )
      50px 50px,
    radial-gradient(hsla(0, 100%, 30%, 0.8) 20%, hsla(0, 100%, 20%, 0)) 50px 0,
    radial-gradient(hsla(0, 100%, 30%, 0.8) 20%, hsla(0, 100%, 20%, 0)) 0 50px,
    radial-gradient(hsla(0, 100%, 20%, 1) 35%, hsla(0, 100%, 20%, 0) 60%) 50px 0,
    radial-gradient(hsla(0, 100%, 20%, 1) 35%, hsla(0, 100%, 20%, 0) 60%) 100px
      50px,
    radial-gradient(hsla(0, 100%, 15%, 0.7), hsla(0, 100%, 20%, 0)) 0 0,
    radial-gradient(hsla(0, 100%, 15%, 0.7), hsla(0, 100%, 20%, 0)) 50px 50px,
    linear-gradient(
        45deg,
        hsla(0, 100%, 20%, 0) 49%,
        hsla(0, 100%, 0%, 1) 50%,
        hsla(0, 100%, 20%, 0) 70%
      )
      0 0,
    linear-gradient(
        -45deg,
        hsla(0, 100%, 20%, 0) 49%,
        hsla(0, 100%, 0%, 1) 50%,
        hsla(0, 100%, 20%, 0) 70%
      )
      0 0;
  background-color: #300;
  background-size: 100px 100px;
  background-position: cover;
  color: ghostwhite;
`;

const Spacer = styled.div`
  min-height: 100px;
  height: fit-content;
  padding: 1rem;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  div {
    display: flex;
    justify-content: space-around;
    gap: 1rem;
    padding: 1rem;
    @media only screen and (max-width: 800px) {
      flex-direction: column;
    }
    ol {
      min-width: 15ch;
    }
    p {
      font-size: 1.1rem;
      max-width: 50ch;
    }
  }

  h1 {
    text-align: center;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-transform: uppercase;
    font-style: italic;
    font-size: calc(3vmax + 0.5rem);
    background-image: linear-gradient(
      45deg,
      #85622a,
      #e6a644,
      #e18339,
      #b6571d,
      #8c3313
    );
    background-clip: text;
    -webkit-background-clip: text;

    color: transparent;
    background-size: 400%;
    animation: bg-animate 20s infinite alternate;

    @keyframes bg-animate {
      0% {
        background-position: left;
      }
      100% {
        background-position: right;
      }
    }
  }
`;

const StartDiv = styled.div`
  height: 0;
`;
function Menu() {
  return (
    <Container>
      <StartDiv id="section1" />
      <Navbar />
      <Spacer>
        <h1>Commitment to Cooking</h1>
      </Spacer>
      <Body />
      <Spacer>
        <div>
          <ul>
            <li> Must-eat! Stewed beef tendon with Ebiske ... 580 yen</li>
            <li> That Fukuda koppe-pan ... 200 yen</li>
            <li> Pure coffee shop Vacheron Napolitan ... 680 yen</li>
            <li> Namami Sparkling ... 550 yen</li>
            <li> Homemade seasonal cider ... 550 yen each</li>
            <li>
              More than 50 kinds of wine bottles at all times ... 2,500 yen
            </li>
          </ul>
          <p>
            There are 60 seats in the store, including counters and table seats.
            Banquets are OK for up to 20 people! You can also charter, so please
            feel free to contact us ♪
          </p>
        </div>
      </Spacer>
      <Footer />
    </Container>
  );
}

export default Menu;
