export function insertPerson(data) {
  let persons = getAllPersons();
  data["id"] = generatepersonsId();
  persons.push(data);
  localStorage.setItem(KEYS.persons, JSON.stringify(persons));
}

export function generatepersonsId() {
  if (localStorage.getItem(KEYS.personsId) == null)
    localStorage.setItem(KEYS.personsId, "0");
  var id = parseInt(localStorage.getItem(KEYS.personsId));
  localStorage.setItem(KEYS.personsId, (++id).toString());
  return id;
}

export function getAllPersons() {
  if (localStorage.getItem(KEYS.persons) == null)
    localStorage.setItem(KEYS.persons, JSON.stringify([]));
  return JSON.parse(localStorage.getItem(KEYS.persons));
}
