import { Grid } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import Controls from "../../../Controls/Controls";
import { useForm, Form } from "../../../useForm";
import * as personsNumber from "./personsNumber";
import * as timeOptions from "./timeOptions";
import Notification from "../../../Controls/Notification";

const initialFValues = {
  id: 0,
  fullName: "",
  email: "",
  mobile: "",
  persons: "",
  time: "",
  date: new Date(),
};

export default function ReserveForm() {
  const validate = (fieldValues = values) => {
    let temp = { ...errors };
    if ("fullName" in fieldValues)
      temp.fullName = fieldValues.fullName ? "" : "This field is reqired.";
    if ("email" in fieldValues)
      temp.email = /$^|.+@.+..+/.test(fieldValues.email)
        ? ""
        : "Email is not valid.";
    if ("mobile" in fieldValues)
      temp.mobile =
        fieldValues.mobile.length > 9 ? "" : "Minimum 10 number requiered.";
    if ("persons" in fieldValues)
      temp.persons =
        fieldValues.persons.length != 0 ? "" : "This field is reqired.";
    if ("time" in fieldValues)
      temp.time = fieldValues.time.length != 0 ? "" : "This field is reqired.";
    setErrors({
      ...temp,
    });

    if (fieldValues == values) return Object.values(temp).every((x) => x == "");
  };

  const {
    values,
    setValues,
    errors,
    setErrors,
    handleInputChange,
    resetForm,
  } = useForm(initialFValues, true, validate);

  const handleSubmit = (e) => {
    e.preventDefault();
    if (validate()) personsNumber.insertPerson(values);
    if (validate()) timeOptions.insertTime(values);
    if (validate())
      setNotify({
        isOpen: true,
        message: "Submitted Successfully",
        type: "success",
      });
  };

  const [notify, setNotify] = useState({
    isOpen: false,
    message: "",
    type: "",
  });

  return (
    <Form onSubmit={handleSubmit}>
      <Grid container direction="column" justify="center">
        <Notification notify={notify} setNotify={setNotify} />
        <Controls.Input
          name="fullName"
          label="Full Name"
          value={values.fullName}
          onChange={handleInputChange}
          error={errors.fullName}
        />
        <Controls.Input
          name="email"
          label="Email"
          value={values.email}
          onChange={handleInputChange}
          error={errors.email}
        />
        <Controls.Input
          name="mobile"
          label="Mobile"
          value={values.mobile}
          onChange={handleInputChange}
          error={errors.mobile}
        />
        <Controls.Select
          name="persons"
          label="Persons"
          value={values.persons}
          onChange={handleInputChange}
          options={personsNumber.getPersonsNumber()}
          error={errors.persons}
        />
        <Grid container spacing={1} direction="row" justify="space-between">
          <Grid item xs={12} sm={6}>
            {" "}
            <Controls.Select
              name="time"
              label="Time"
              value={values.time}
              onChange={handleInputChange}
              options={timeOptions.getTimeOptions()}
              error={errors.time}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <Controls.DatePicker
              name="date"
              label="Date"
              value={values.date}
              onChange={handleInputChange}
            />
          </Grid>
        </Grid>

        <div>
          <Controls.Button type="submit" text="Submit" />
          <Controls.Button text="Reset" color="default" onClick={resetForm} />
        </div>
      </Grid>
    </Form>
  );
}
