import styled from "styled-components";
import "../../../index.css";

export const Container = styled.div`
  height: fit-content;
  width: 100%;
  overflow-x: hidden;
  padding-bottom: 5rem;
`;

export const Banner = styled.div`
  height: 70vh;
  width: 100%;
  background: url("http://www.southfloridaceo.com/wp-content/uploads/2017/04/fine-dining-1280x640.jpeg")
    no-repeat;
  background-size: cover;

  h1 {
    width: fit-content;
    border-bottom: 1px solid white;
    border-top: 1px solid white;
    font-family: "Playfair Display", serif;
    font-weight: 200;
    font-size: calc(3vmax + 1rem);
    position: absolute;
    left: 45%;
    top: 30%;
    padding: 1rem;
    color: ghostwhite;
    @media only screen and (max-width: 768px) {
      margin: auto;
      left: 0;
      position: relative;
    }
  }
`;

export const Reserve = styled.div`
  height: fit-content;
  width: fit-content;
  max-width: fit-content;
  display: flex;
  justify-content: space-around;
  background: url("https://live.staticflickr.com/44/167285658_e9d493f658_b.jpg")
    no-repeat center center;
  background-size: cover;
  margin: auto;
  position: relative;
  top: -70px;
  gap: calc(3vmax + 1rem);
  padding: 2rem calc(5vmax + 1rem);

  @media only screen and (max-width: 600px) {
    flex-direction: column;
    padding: 2rem 10vw;
  }
`;

export const Table = styled.div`
  position: relative;
  color: gainsboro;
  table {
    margin: auto;
  }

  tr:nth-child(even) {
    background: rgba(0, 0, 0, 0.2);
  }

  table,
  th,
  td {
    border: 1px solid palegoldenrod;
    padding: 0.2rem 1rem;
  }
`;
