import styled from "styled-components";

export const Div = styled.div`
  z-index: 99;
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 0) 100%
  );
  position: sticky;
  top: 0;
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;

  img {
    max-width: 180px;
    transform: translateY(-200%);
    opacity: 0;
  }
  @media only screen and (max-width: 768px) {
    .pepper {
      display: none;
    }
    .ebilogo {
      display: block;
      margin: 0 auto 0 1rem;
    }
  }

  div {
    a {
      color: darkorange;
      font-weight: 500;
      letter-spacing: 2px;
      :after,
      :before {
        content: "|";
        position: absolute;
        transition: all 0.3s ease-in-out;
        visibility: hidden;
        opacity: 0;
      }
    }

    a:hover {
      :after,
      :before {
        visibility: visible;
        opacity: 1;
        color: darkorange;
      }
      :after {
        transform: translateX(1rem);
      }
      :before {
        transform: translateX(-1rem);
      }
    }
  }
`;

export const MenuLarge = styled.div`
  display: flex;
  justify-content: space-around;
  flex: 1;
  @media only screen and (max-width: 768px) {
    display: none;
  }
`;

export const MenuSmall = styled.div`
  display: none;
  position: fixed;
  right: 20px;
  top: 10px;

  a {
    display: block;
  }
  @media only screen and (max-width: 768px) {
    display: block;
  }
`;

export const Burger = styled.div`
  position: relative;
  width: 3rem;
  height: 2rem;
  opacity: 0.5;
  ${MenuSmall}:hover & {
    div {
      transform: rotate(90deg);
      border-bottom: 5px solid darkred;
    }
    div::before {
      transform: rotate(45deg);
      border-bottom: 5px solid darkred;
    }
    div::after {
      transform: rotate(315deg);
      border-bottom: 5px solid darkred;
    }
    opacity: 1;
  }

  div {
    border-bottom: 5px solid gainsboro;
    border-radius: 10px;
    width: 3rem;
    position: relative;
    top: 40%;
    transition: all 0.5s;

    :before,
    :after {
      content: "";
      border-bottom: 5px solid gainsboro;
      border-radius: 10px;
      width: 3rem;
      position: absolute;
      transition: all 0.5s;
    }

    :before {
      transform: translateY(-10px);
    }
    :after {
      transform: translateY(10px);
    }
  }
`;

export const BurgerMenu = styled.div`
  top: 100%;
  left: -200%;

  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: fit-content;
  height: fit-content;
  transform: scale(0);
  transform-origin: top right;
  transition: all 0.5s ease-in-out;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.5970763305322129) 100%
  );
  a {
    margin: 1em;
  }

  ${MenuSmall}:hover & {
    transform: scale(1);
  }
`;

export const Start = styled.a`
  position: fixed;
  z-index: 99;
  right: 2rem;
  bottom: 0;
  font-size: 3rem;
  font-weight: bold;
  transform: translateY(5vmax);
  opacity: 0;
  color: rgba(255, 255, 255, 0.1);
  :hover {
    color: #ffa500;
  }
`;
