import React, { useEffect, useState } from "react";
import Addressbar from "../Addressbar";
import Navbar from "../Navbar/Navbar";
import Body from "./Body/Body";
import styled from "styled-components";
import Footer from "../Footer/Footer";

const Container = styled.div`
  height: 100%;
  width: 100%;
  scroll-snap-type: y mandatory;
  position: relative;
  background-color: #996515;
  background-image: linear-gradient(
      30deg,
      #cba135 12%,
      transparent 12.5%,
      transparent 87%,
      #cba135 87.5%,
      #cba135
    ),
    linear-gradient(
      150deg,
      #cba135 12%,
      transparent 12.5%,
      transparent 87%,
      #cba135 87.5%,
      #cba135
    ),
    linear-gradient(
      30deg,
      #cba135 12%,
      transparent 12.5%,
      transparent 87%,
      #cba135 87.5%,
      #cba135
    ),
    linear-gradient(
      150deg,
      #cba135 12%,
      transparent 12.5%,
      transparent 87%,
      #cba135 87.5%,
      #cba135
    ),
    linear-gradient(
      60deg,
      #fbf4b5 25%,
      transparent 25.5%,
      transparent 75%,
      #fbf4b5 75%,
      #fbf4b5
    ),
    linear-gradient(
      60deg,
      #fbf4b5 25%,
      transparent 25.5%,
      transparent 75%,
      #fbf4b5 75%,
      #fbf4b5
    );
  background-size: 40px 70px;
  background-position: 0 0, 0 0, 20px 35px, 20px 35px, 0 0, 20px 35px;
  color: ghostwhite;
`;

function Home() {
  return (
    <Container>
      <Addressbar />
      <Navbar />
      <Body />
      <Footer />
    </Container>
  );
}

export default Home;
