import React, { useEffect, useState } from "react";
import { Div, MenuSmall, MenuLarge, Burger, BurgerMenu, Start } from "./Style";
import { Link } from "react-router-dom";

function Navbar() {
  const [show, handleShow] = useState(false);

  useEffect(() => {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 300) {
        handleShow(true);
      } else handleShow(false);
    });
    return () => {
      window.removeEventListener("scroll", Navbar, true);
    };
  }, []);

  return (
    <Div className={`${show && "nav__black"}`}>
      <Link to="/" className="ebilogo">
        <img src="/images/cplogo.png" alt="" />
      </Link>
      <MenuLarge>
        <Link to="/">Home</Link>
        <Link to="/menu">Menu</Link>
        <Link to="/reservation">Reservation</Link>
        <Link to="/info">Info</Link>
      </MenuLarge>
      <MenuSmall>
        <Burger>
          <div />
        </Burger>
        <BurgerMenu>
          <Link to="/">Home</Link>
          <Link to="/menu">Menu</Link>
          <Link to="/reservation">Reservation</Link>
          <Link to="/info">Info</Link>
        </BurgerMenu>
      </MenuSmall>
      <a
        className="pepper"
        href="https://www.hotpepper.jp/strJ001116666/"
        target="_blank"
      >
        <img
          src="https://www.recruit-lifestyle.co.jp/uploads/en/sites/2/2015/07/tit_hotpeppergourmet_logo01.png"
          alt=""
        />
      </a>
      <Start href="#section1" className={`${show && "Start"}`}>
        ^
      </Start>
    </Div>
  );
}

export default Navbar;
