const KEYS = {
  time: "time",
  timeId: "timeId",
};

export const getTimeOptions = () => [
  { id: "1", title: "5:00 pm" },
  { id: "2", title: "5:30 pm" },
  { id: "3", title: "6:00 pm" },
  { id: "4", title: "6:30 pm" },
  { id: "5", title: "7:00 pm" },
  { id: "6", title: "7:30 pm" },
  { id: "7", title: "8:00 pm" },
  { id: "8", title: "8:30 pm" },
  { id: "9", title: "9:00 pm" },
  { id: "10", title: "9:30 pm" },
  { id: "11", title: "10:00 pm" },
  { id: "12", title: "10:30 pm" },
  { id: "13", title: "11:00 pm" },
];

export function insertTime(data) {
  let time = getAlltime();
  data["id"] = generatetimeId();
  time.push(data);
  localStorage.setItem(KEYS.time, JSON.stringify(time));
}

export function generatetimeId() {
  if (localStorage.getItem(KEYS.timeId) == null)
    localStorage.setItem(KEYS.timeId, "0");
  var id = parseInt(localStorage.getItem(KEYS.timeId));
  localStorage.setItem(KEYS.timeId, (++id).toString());
  return id;
}

export function getAlltime() {
  if (localStorage.getItem(KEYS.time) == null)
    localStorage.setItem(KEYS.time, JSON.stringify([]));
  return JSON.parse(localStorage.getItem(KEYS.time));
}
