import styled from "styled-components";

export const Container = styled.div`
  height: fit-content;
  max-width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  position: relative;
  background-color: rgb(32, 32, 32);
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 1),
      rgba(0, 0, 0, 0) 20%,
      rgba(0, 0, 0, 0) 80%,
      rgba(0, 0, 0, 1)
    ),
    linear-gradient(
      45deg,
      black 25%,
      transparent 25%,
      transparent 75%,
      black 75%,
      black
    ),
    linear-gradient(
      45deg,
      black 25%,
      transparent 25%,
      transparent 75%,
      black 75%,
      black
    ),
    linear-gradient(to bottom, rgb(8, 8, 8), rgb(32, 32, 32));
  background-size: 100% 100%, 10px 10px, 10px 10px, 10px 5px;
  background-position: 0px 0px, 0px 0px, 5px 5px, 0px 0px;
  .MuiTabs-root {
    background: #4e301b !important;

    .MuiTab-wrapper {
      color: palegoldenrod !important;
    }
  }
`;

export const Side = styled.div`
  flex: 2;
  position: relative;
  top: 0;
  bottom: 0;
  height: 100%;
  /* max-width: 500px; */
  img {
    max-width: 400px;
  }
  .coursePanel {
    position: relative;
    width: auto;
    height: 100%;
  }
  .drinkstab {
    display: flex;
    align-items: center;
    .drinks {
      height: 1200px;
    }
  }
  /* 
  .wine {
    img {
      max-width: 400px;
    }
  } */

  @media only screen and (max-width: 1300px) {
    display: none;
  }
`;
export const Main = styled.div`
  flex: 5;
  background: #321;
  min-width: 280px;
`;

export const Scroll = styled.section`
  width: 100%;
  z-index: 10;
  display: none;
  background: rgba(0, 0, 0, 0.2);
  @media only screen and (max-width: 665px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  a {
    margin: 0 1rem;
    transition: all 0.3s ease-out;
    padding: 0.2rem 0.5rem;
    border-bottom: 1px solid gray;
    :hover {
      transition: all 0.25s ease-out;
      transform: scale(1.05);
    }
  }
`;
