import React from "react";
import styled from "styled-components";

const Div = styled.div`
  background-color: black;
  text-align: center;
  padding: 0.1rem 0;
  color: white;
  font-size: 0.8rem;
  scroll-snap-align: start;

  @media (max-width: 768px) {
    p {
      display: none;
    }
  }
`;

function Addressbar() {
  return (
    <Div id="section1">
      <p>
        10-6 Moriokaekimaedori, Morioka City, Iwate Prefecture Malkan Pet
        Building 1F (019-601-8127)
      </p>
    </Div>
  );
}

export default Addressbar;
